.navbar41-container {
  width: 100%;
  display: flex;
  position: relative;
  justify-content: center;
  background-color: var(--dl-color-theme-neutral-light);
}
 
.navbar41-navbar-interactive {
  width: 100%;
  display: flex;
  max-width: var(--dl-size-size-maxwidth);
  align-items: center;
  padding-top: var(--dl-space-space-twounits);
  padding-left: var(--dl-space-space-threeunits);
  padding-right: var(--dl-space-space-threeunits);
  padding-bottom: var(--dl-space-space-twounits);
  justify-content: space-between;
}
 
.navbar41-image1 {
  height: 3rem;
}
 
.navbar41-desktop-menu {
  flex: 1;
  display: flex;
  justify-content: space-between;
}
 
.navbar41-links1 {
  gap: var(--dl-space-space-twounits);
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: var(--dl-space-space-twounits);
  flex-direction: row;
  justify-content: space-between;
}

.pricing14-tabs-top {
  display: flex;
  gap: 0;
  margin-left: auto;
  align-items: center;
}

.pricing14-tabs-mobile {
  display: flex;
  gap: 0;
  margin-top: var(--dl-space-space-unit);
  align-items: center;
}

.pricing14-button10-mobile,
.pricing14-button11-mobile,
.pricing14-button12-mobile,
.pricing14-button13-mobile {
  padding: var(--dl-space-space-halfunit) var(--dl-space-space-unit);
  font-size: 14px;
}

nav div{
  display: flex;
  align-items: end;
}
 
.navbar41-buttons1 {
  gap: var(--dl-space-space-twounits);
  display: flex;
  align-items: center;
  margin-left: var(--dl-space-space-twounits);
}
 
.navbar41-action11 {
  display: flex;
  flex-direction: row;
}
 
.navbar41-action21 {
  display: flex;
  flex-direction: row;
}
 
.navbar41-burger-menu {
  display: none;
}
 
.navbar41-icon1 {
  width: var(--dl-size-size-xsmall);
  height: var(--dl-size-size-xsmall);
}
 
.navbar41-mobile-menu {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  display: none;
  padding: var(--dl-space-space-twounits);
  z-index: 100;
  position: absolute;
  flex-direction: column;
  background-color: var(--dl-color-theme-neutral-light);
}
 
.navbar41-nav {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
 
.navbar41-top {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: var(--dl-space-space-threeunits);
  justify-content: space-between;
}
 
.navbar41-logo {
  height: 3rem;
}
 
.navbar41-close-menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
 
.navbar41-icon3 {
  width: var(--dl-size-size-xsmall);
  height: var(--dl-size-size-xsmall);
}
 
.navbar41-links2 {
  gap: var(--dl-space-space-unit);
  flex: 0 0 auto;
  display: flex;
  align-self: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
 
.navbar41-buttons2 {
  gap: var(--dl-space-space-twounits);
  display: flex;
  margin-top: var(--dl-space-space-twounits);
  align-items: center;
}
 
.navbar41-text3 {
  display: inline-block;
}
 
.navbar41-text4 {
  display: inline-block;
}
 
.navbar41-text5 {
  display: inline-block;
}
 
.navbar41-text6 {
  display: inline-block;
}
 
.navbar41-text7 {
  display: inline-block;
}
 
.navbar41-text8 {
  display: inline-block;
}
 
.navbar41-text9 {
  display: inline-block;
}
 
@media(max-width: 767px) {
  .navbar41-navbar-interactive {
    padding-left: var(--dl-space-space-twounits);
    padding-right: var(--dl-space-space-twounits);
  }
  .navbar41-desktop-menu {
    display: none;
  }
  .navbar41-burger-menu {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
 
@media(max-width: 479px) {
  .navbar41-navbar-interactive {
    padding: var(--dl-space-space-unit);
  }
  .navbar41-mobile-menu {
    padding: var(--dl-space-space-unit);
  }
}
